* {
  margin: 0;
  padding: 0;
}

li, a, button {
  font-family: "Josefin Sans";
  font-weight: 500;
  font-size: 20px;
  color:darkgrey;
  text-decoration: none;
}

.nav_bar {
  display: flex;
  justify-content: flex-end;
  align-items: center; 
  background-color: rgba(71, 66, 66, 0.938);
}

.logo {
  cursor: pointer;
  margin-right: auto;
}

.nav_links {
  list-style: none;
}

.nav_links li {
  display: inline-block;
  padding: 0px 20px;
}

.nav_links li a {
  transition: all 0.3s ease 0s;
}

.nav_links li a:hover {
  color: black;
}

.header_div , .about_div , footer {
  min-height: 100vh;
  display: flex;
}

/* HEADER */
.section1 {
  background-color: rgb(255, 187, 0);
  min-height: 100vh;
}

.header_div_1, .header_div_2 {
  min-width: 50%;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  align-items: center;
  justify-content: center;
}



/* ABOUT */
.about_div {
  background-color: rgb(226, 225, 223);
}

/* SERVICES */
.services_div {
  background-color: rgb(190, 140, 3);
  display: flex;
  flex-wrap: wrap;
  min-height: 30vh;
}

.services_div_1, .services_div_2, .services_div_3 {
  margin: auto;
  width: 500px;
  border-width: 3px solid black;
}

/* FOOTER */
footer {
  min-height: 30vh;
  background-color: black;
  color: white;
}
@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap');